<template>
  <div
    v-if="isPlaceholderVisible"
    class="bg-slate-400"
  ></div>
  <img
    v-else
    :src="props.src"
    @error="isLoadingFailed = true"
  />
</template>

<script setup lang="ts">
const props = defineProps<{
  src?: string
}>()

const isLoadingFailed = ref(false)
const isPlaceholderVisible = computed(() => !props.src || isLoadingFailed.value)
</script>
